    showDialog(title: string, message: string) {
      let buttons = [{
        text: "ok",
        handler: () => {
        }
      }];

      const alert: any = this.alertCtrl.create({
        header: title,
        message: message,
        buttons: buttons
    }).then((alert) => alert.present());
    }
